import styles from "./Header.module.scss";

export const Header = ({ target, type, handleOpen }) => {
  return (
    <button
      type="button"
      onClick={() => handleOpen(target)}
      className={`${styles.header} ${type === target && styles.header_current}`}
    >
      {target === "data"
        ? ""
        : target === "posts"
        ? ""
        : target === "follows"
        ? ""
        : target === "likes"
        ? ""
        : target === "outputs"
        ? ""
        : target === "entries" && ""}
    </button>
  );
};
