import styles from "./Item.module.scss";
import root from "../Main.module.scss";

import { useFormContext } from "react-hook-form";

export const Belong = () => {
  const {
    register,
    formState: { errors },
  } = useFormContext();

  return (
    <div className={`${root.main_col} ${root.main_col_mid}`}>
      <span className={root.main_tag}></span>
      <div className={styles.item}>
        <input
          placeholder="Hit me up "
          className={`${styles.item_input} ${
            errors.belong && styles.item_input_error
          }`}
          {...register("belong", {
            required: {
              value: true,
              message: "",
            },
            pattern: {
              value: /^\S+/,
              message: "",
            },
            minLength: {
              value: 2,
              message: "2",
            },
            maxLength: {
              value: 24,
              message: "24",
            },
          })}
        />
        {errors.belong?.message && (
          <span className={styles.item_error}>{errors.belong?.message}</span>
        )}
      </div>
    </div>
  );
};
