import styles from "./Item.module.scss";
import root from "../Main.module.scss";

import { useFormContext } from "react-hook-form";

export const Station = () => {
  const {
    register,
    formState: { errors },
  } = useFormContext();

  return (
    <div className={`${root.main_col} ${root.main_col_mid}`}>
      <span className={root.main_tag}></span>
      <div className={styles.item}>
        <input
          placeholder=""
          className={`${styles.item_input} ${
            errors.station && styles.item_input_error
          }`}
          {...register("station", {
            pattern: {
              value: /^\S+/,
              message: "",
            },
            minLength: {
              value: 2,
              message: "2",
            },
            maxLength: {
              value: 24,
              message: "24",
            },
            validate: {
              station: (value) => value.match(/^(?!.*$).*$/),
            },
          })}
        />

        {errors.station?.type === "station" && (
          <span className={styles.item_error}></span>
        )}
        {errors.station?.message && (
          <span className={styles.item_error}>{errors.station?.message}</span>
        )}
      </div>
    </div>
  );
};
