import styles from "../Form.module.scss";
import { useFormContext } from "react-hook-form";

export const Name = () => {
  const {
    register,
    formState: { errors },
  } = useFormContext();

  return (
    <div className={styles.form_col}>
      <span className={styles.form_tag}></span>
      <div>
        <input
          className={`${styles.form_input} ${
            errors.name && styles.form_input_error
          }`}
          placeholder="Hitmeup"
          {...register("name", {
            required: {
              value: true,
              message: "",
            },
            pattern: {
              value: /^\S+/,
              message: "",
            },
            maxLength: {
              value: 32,
              message: "32",
            },
          })}
        />
        {errors.name?.message && (
          <span className={styles.form_error}>{errors.name?.message}</span>
        )}
      </div>
    </div>
  );
};
