import styles from "./Item.module.scss";
import root from "../Main.module.scss";

import { useFormContext } from "react-hook-form";

export const Body = ({ index }) => {
  const {
    register,
    formState: { errors },
  } = useFormContext();

  return (
    <div className={root.main_col}>
      <span className={root.main_tag}>
        {index === "matters" && ""}
        {index === "resources" && "PR"}
      </span>
      <div className={styles.item}>
        <textarea
          className={`${styles.item_textarea} ${
            errors.body && styles.item_textarea_error
          }`}
          {...register(
            "body",
            index === "matters"
              ? {
                  required: {
                    value: true,
                    message: "",
                  },
                  pattern: {
                    value: /^\S+/,
                    message: "",
                  },
                  minLength: {
                    value: 16,
                    message: "16",
                  },
                  maxLength: {
                    value: 600,
                    message: "600",
                  },
                }
              : index === "resources" && {
                  required: {
                    value: true,
                    message: "PR",
                  },
                  pattern: {
                    value: /^\S+/,
                    message: "",
                  },
                  minLength: {
                    value: 16,
                    message: "16",
                  },
                  maxLength: {
                    value: 600,
                    message: "600",
                  },
                }
          )}
        ></textarea>
        {errors.body?.message && (
          <span className={styles.item_error}>{errors.body?.message}</span>
        )}
      </div>
    </div>
  );
};
