import styles from "./Item.module.scss";
import root from "../Main.module.scss";

import { useFormContext } from "react-hook-form";

export const Status = () => {
  const {
    register,
    formState: { errors },
  } = useFormContext();

  return (
    <div className={root.main_grid}>
      <div className={root.main_col}>
        <span className={root.main_tag}></span>
        <div className={`${styles.item} ${styles.item_select}`}>
          <select
            className={`${styles.item_input} ${
              errors.status && styles.item_input_error
            }`}
            {...register("status", {
              required: {
                value: true,
                message: "",
              },
            })}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>

          {errors.status?.message && (
            <span className={styles.item_error}>{errors.status?.message}</span>
          )}
        </div>
      </div>
    </div>
  );
};
